@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
  background: #E5E5E5; }

main {
  flex-grow: 1; }

header {
  flex-grow: 0; }

footer {
  flex-grow: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  margin: 0 auto;
  width: 1460px;
  max-width: 100%; }

.section {
  padding: 72px 70px 20px;
  background-color: #fff; }

.section__caption {
  margin: 0;
  margin-bottom: 34px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.1;
  letter-spacing: 0;
  color: #3D3D3D; }

.section__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.section__card {
  flex: 0 0 325px;
  width: 325px;
  margin-bottom: 42px; }

@media screen and (max-width: 1338px) {
  .section__row {
    justify-content: space-around; } }

@media screen and (max-width: 991px) {
  .section {
    padding: 72px 40px 20px; }
  .section__caption {
    margin-bottom: 45px; }
  .section__card {
    margin-bottom: 52px; } }

@media screen and (max-width: 767px) {
  .section {
    padding: 72px 20px 20px; }
  .section__row {
    display: block; }
  .section__card {
    margin-left: auto;
    margin-right: auto;
    width: 320px; } }

@media screen and (max-width: 359px) {
  .section__card {
    width: auto;
    margin-bottom: 51px; } }

.card {
  padding: 10px;
  max-width: 100%;
  min-height: 416px;
  display: flex;
  flex-direction: column; }
  .card:hover .card__link {
    color: #4A6EED; }

.card__img {
  position: relative;
  width: 100%;
  height: 200px;
  margin-bottom: 40px; }
  .card__img img {
    object-fit: contain;
    object-position: center;
    width: 100%;
    height: 100%; }

.card__like {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 16px;
  top: 5px;
  z-index: 1;
  width: 25px;
  width: 25px;
  border-radius: 50%;
  background-color: #fff; }
  .card__like svg {
    fill: none;
    stroke: #525252; }
  .card__like.active svg {
    stroke: #FF0D55; }
  .card__like:hover, .card__like:focus, .card__like:active {
    cursor: pointer;
    opacity: 0.6; }

.card__cash {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0;
  color: #141414;
  margin-bottom: 8px; }
  .card__cash.sale {
    color: #FF0D55; }
    .card__cash.sale span {
      position: relative;
      margin-left: 8px;
      font-family: "Roboto", "Arial", sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 1.1;
      letter-spacing: 0;
      color: #8F8F8F;
      overflow: hidden; }
      .card__cash.sale span::after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        background-color: #FF0D55;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 49%;
        transform: translate(-50%, -50%) rotate(-13deg); }

.card__link {
  display: block;
  margin-bottom: 7px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0;
  color: #525252; }
  .card__link:hover, .card__link:focus, .card__link:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }

.card__basket {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0.01em;
  color: #ffffff;
  margin: auto auto 0;
  width: 156px;
  height: 44px;
  border-radius: 3px;
  background-color: #1A45DB; }
  .card__basket:focus, .card__basket:hover {
    cursor: pointer;
    background: #173DC2; }
  .card__basket:active {
    background: #1435A8; }

.card__btn-block {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: auto; }

.card__btn {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 3px;
  background: #F8F8F8; }
  .card__btn svg {
    fill: #1A45DB; }
  .card__btn:focus, .card__btn:hover {
    cursor: pointer;
    background-color: #F2F2F2; }
    .card__btn:focus svg, .card__btn:hover svg {
      fill: #173DC2; }
  .card__btn:active {
    background-color: #EDEDED; }
    .card__btn:active svg {
      fill: #1435A8; }

.card__value {
  padding: 5px;
  flex: 0 0 68px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0;
  color: #525252;
  text-align: center; }

@media screen and (max-width: 991px) {
  .card {
    padding: 0;
    min-height: 397px; }
  .card__cash {
    margin-bottom: 12px; } }

@media screen and (max-width: 767px) {
  .card {
    min-height: 0; }
  .card__link {
    margin-bottom: 19px; } }
